import React, { useEffect } from 'react';
import './../css/GraphicEditor.css'
import curveEditor from '../objects/CurveEditor.js';
import circleEditor from '../objects/CircleEditor.js';
import rectangleEditor from '../objects/RectangleEditor.js';
import ThemeContext from '../context/ThemeContext';



export default function GraphicEditor() {

    let self = null
    let context = null
    
    let tools = [
        circleEditor,
        curveEditor,
        rectangleEditor,
    ]

    let down = () => {
        tools[Math.floor(Math.random() * 3) + 0].down(context, self)
        
        // rectangleEditor.down(context, self)//FiSquare||FaRegSquare||GiSquare||IoIosSquareOutline||IoMdSquareOutline||MdCropSquare||FaVectorSquare
        // curveEditor.down(context, self)//FaBezierCurve||AiOutlineNodeIndex||RiLoader5Line
        // circleEditor.down(context, self)//BsCircle||FiCircle||FaRegCircle||GiCircle||RiCheckboxBlankCircleLine
    }

    return (
        <ThemeContext.Consumer>
        {(obj)=>(
            <canvas className="GraphicEditor" width="2000" height="2000" onMouseDown={down} ref={elem =>{self = elem; context = obj}}>
                
            </canvas>
        )}
        </ThemeContext.Consumer>    
    )
}
