import Mouse from './MouseHandle.js'
let editor = null
let container = null
let strokeStyle = 'rgba(255, 255, 255, 1)'
class RectangleEditor{
    draw(coordinates){
        let canvas = editor
        coordinates.x3 = Mouse.getCursor(true).x
        coordinates.y3 = Mouse.getCursor(true).y
        coordinates.x2 = (coordinates.x1+Mouse.getCursor(true).x)/2
        coordinates.y2 = (coordinates.y1+Mouse.getCursor(true).y)/2
        let ctx = canvas.getContext("2d");
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.strokeStyle = strokeStyle
        ctx.lineWidth = 2
        ctx.beginPath();
        ctx.rect(
            coordinates.x1,
            coordinates.y1,
            coordinates.x3-coordinates.x1,
            coordinates.y3-coordinates.y1
        );
        ctx.stroke();
    }
    
    print(context){
        if(context.graphicEditor === 'fede'){
            return
        }
        let canvas = container
        let coordinates = context.graphicEditor[1]
        let ctx = canvas.getContext("2d");       
        // ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.strokeStyle = strokeStyle
        ctx.lineWidth = 2
        ctx.beginPath();
        ctx.rect(
            coordinates.x1,
            coordinates.y1,
            coordinates.x3-coordinates.x1,
            coordinates.y3-coordinates.y1
        );      
        ctx.stroke();
    }

    down(context, self){
        editor = self
        container = context.draw
        const coordinates = {
            x1:null,y1:null,
            x2:null,y2:null,
            x3:null,y3:null
        }
        coordinates.x1 = Mouse.getCursor(true).x
        coordinates.y1 = Mouse.getCursor(true).y
        this.print(context)
        Mouse.setMoveSubscriber(()=>{this.draw(coordinates)})
        Mouse.setUpSubscriber(()=>{
            context.pointers.p1.style.left = coordinates.x1+'px'
            context.pointers.p1.style.top = coordinates.y1+'px'
            context.pointers.p2.style.left = coordinates.x2+'px'
            context.pointers.p2.style.top = coordinates.y2+'px'
            context.pointers.p3.style.left = coordinates.x3+'px'
            context.pointers.p3.style.top = coordinates.y3+'px'
            context.monitorGraph.x1.value = coordinates.x1
            context.monitorGraph.y1.value = coordinates.y1
            context.monitorGraph.x2.value = coordinates.x2
            context.monitorGraph.y2.value = coordinates.y2
            context.monitorGraph.x3.value = coordinates.x3
            context.monitorGraph.y3.value = coordinates.y3
            context.setGraphicEditor([editor, coordinates])
            Mouse.setMoveSubscriber(null)
        })
    }
}

const rectangleEditor = new RectangleEditor()

export default rectangleEditor