import React, {useState} from 'react'
import './App.css';
import Grid from './components/Grid.js'
import TopBar from './components/TopBar.js'
import KeyBoard from './components/KeyBoard.js'
import Monitor from './components/Monitor.js'
import ElementContainer from './components/ElementContainer.js'
import GraphicEditor from './components/GraphicEditor.js';
import GraphicPoints from './components/GraphicPoints.js';
import GraphicsContainer from './components/GraphicsContainer.js';

import ThemeContext from './context/ThemeContext.js';

function App() {
  const [val, setVal] = useState([]);
  const [monitorSign, setMonitorSign] = useState('fede');
  const [pointers, setPointers] = useState('fede');
  const [graphicEditor, setGraphicEditor] = useState('fede');
  const [monitorGraph, setMonitorGraph] = useState('fede');
  const [draw, setDraw] = useState('fede');

  let obj = {
    val, setVal, 
    monitorSign, setMonitorSign, 
    pointers, setPointers, 
    graphicEditor, setGraphicEditor,
    monitorGraph, setMonitorGraph,
    draw, setDraw
  }

  return (
    <div className="App">
      <ThemeContext.Provider value={obj} >
        <Grid></Grid>
        <TopBar></TopBar>
        <Monitor></Monitor>
        <KeyBoard></KeyBoard>
        <GraphicsContainer></GraphicsContainer>
        <GraphicEditor></GraphicEditor>
        <GraphicPoints></GraphicPoints>
        <ElementContainer></ElementContainer>
      </ThemeContext.Provider>
    </div>
  );
}

export default App;
