import React, { useEffect } from 'react';
import ThemeContext from './../context/ThemeContext';
import Mouse from './../objects/MouseHandle.js'

export default function GraphicPoints() {
    let context = null
    let p = []
    let selected = null
    let follower = (e)=>{
        selected.style.left = Mouse.getCursor(true).x+'px'
        selected.style.top = Mouse.getCursor(true).y+'px' 
        setCurve(selected)
    }

    let setCurve = (selected) => {
        let index = selected.className.replace('p','')-1
        let coordinates = context.graphicEditor[1]
        let canvas = context.graphicEditor[0]
        let ctx = canvas.getContext("2d");
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.strokeStyle = 'rgba(255, 255, 255, 1)'
        ctx.lineWidth = 2
        ctx.beginPath();
        if(index === 0){
            coordinates.x1 = Mouse.getCursor(true).x
            coordinates.y1 = Mouse.getCursor(true).y
        }
        if(index === 1){
            coordinates.x2 = Mouse.getCursor(true).x
            coordinates.y2 = Mouse.getCursor(true).y
        }
        if(index === 2){
            coordinates.x3 = Mouse.getCursor(true).x
            coordinates.y3 = Mouse.getCursor(true).y
        }
        ctx.bezierCurveTo(
            coordinates.x1,coordinates.y1,
            coordinates.x2,coordinates.y2,
            coordinates.x3,coordinates.y3,
            ); 
        ctx.stroke();
        context.monitorGraph.x1.value = coordinates.x1
        context.monitorGraph.y1.value = coordinates.y1
        context.monitorGraph.x2.value = coordinates.x2
        context.monitorGraph.y2.value = coordinates.y2
        context.monitorGraph.x3.value = coordinates.x3
        context.monitorGraph.y3.value = coordinates.y3
    }
    let down = (e) => {
        selected = e.target
        Mouse.setMoveSubscriber(e => {follower(e)})
        Mouse.setUpSubscriber(()=>{
            selected = null
            Mouse.setMoveSubscriber(null)
        })
    }
    
    useEffect(() => {
        context.setPointers({
            p1:p[0],
            p2:p[1],
            p3:p[2]
        })
    },[]);

    return (
        <ThemeContext.Consumer>
        {(obj)=>(
            <div>
                <div className="p1" ref={elem =>(p[0] = elem)} onMouseDown={down}></div>
                <div className="p2" ref={elem =>(p[1] = elem)} onMouseDown={down}></div>
                <div className="p3" ref={elem =>{p[2] = elem; context = obj}} onMouseDown={down}></div>
            </div>
        )}
        </ThemeContext.Consumer>
            
    )
}
