import React, { useState, useEffect } from 'react';
import Storage from './LocalStorage.js'
import ThemeContext from './../context/ThemeContext';

export default function MonitorSign({historyElement, obj}) {

    let step = Storage.getLocal('distance')
    const [x, setX] = useState(historyElement.x);
    const [y, setY] = useState(historyElement.y);
    const [size, setSize] = useState(historyElement.size);
    const [content, setContent] = useState(obj.monitorSign);
    let nodeContainer = {
        content:null,
        x:null,
        y:null,
        size:null
    }
    let monitor = null
    useEffect(() => {
        monitor.setMonitorSign({
            content:nodeContainer.content,
            x:nodeContainer.x,
            y:nodeContainer.y,
            size:nodeContainer.size
        })
      },[]);
    return (
        <ThemeContext.Consumer>
        {(obj)=>(
            <div>
                <div><b>Content: </b> <br/><input ref={elem =>(nodeContainer.content = elem)} type="text" value={content} onChange={(e)=>{setContent(e.target.value)}}></input></div>
                <div className="numbers">
                    <div><b>x: </b><input ref={elem =>(nodeContainer.x = elem)} type="number" min="0" max="2000" step={step} value={x} onChange={(e)=>{setX(e.target.value)}} /></div>
                    <div><b>y: </b><input ref={elem =>(nodeContainer.y = elem)} type="number" min="0" max="2000" step={step} value={y} onChange={(e)=>{setY(e.target.value)}} /></div>
                    <div><b>s: </b><input ref={elem =>{nodeContainer.size = elem;monitor = obj}} type="number" min="0" max="200" value={size} onChange={(e)=>{setSize(e.target.value)}} /></div>
                </div>
            </div>
        )}
        </ThemeContext.Consumer>
            
    )
}
