import React, { useState, useEffect } from 'react';
import Storage from './LocalStorage.js'
import { FaAngleDown } from "react-icons/fa";
import { FaAngleUp } from "react-icons/fa";
import { FaAngleLeft } from "react-icons/fa";
import { FaAngleRight } from "react-icons/fa";
import ThemeContext from './../context/ThemeContext';

export default function MonitorGraph({historyElement}) {
    // strokeStyle:'rgba(255, 255, 255, 1)', content:"[10,10,10,10,10]", lineWidth: 15
    let step = Storage.getLocal('distance')
    const [x, setX] = useState(historyElement.x);
    const [y, setY] = useState(historyElement.y);
    const [size, setSize] = useState(historyElement.size);
    const [content, setContent] = useState(historyElement.content);
    


    let nodeContainer = {
        x1:null,
        y1:null,
        x2:null,
        y2:null,
        x3:null,
        y3:null,
    }
    let monitor = null
    useEffect(() => {
        monitor.setMonitorGraph({
            x1:nodeContainer.x1,
            y1:nodeContainer.y1,
            x2:nodeContainer.x2,
            y2:nodeContainer.y2,
            x3:nodeContainer.x3,
            y3:nodeContainer.y3,
        })
    },[]);

    return (
        <ThemeContext.Consumer>
        {(obj)=>(
               <div>
               <div><b>Color: </b> <input type="text" value={'#ff0000'} onChange={(e)=>{setContent(e.target.value)}}></input></div>
               <div><b>LineWidth: </b><input type="number" min="0" max="2000" step={step} value={x} onChange={(e)=>{setX(e.target.value)}} /></div>

               <hr></hr>
               <div className="ponters">
                   <div>
                       <div><b>x: </b><input ref={elem =>(nodeContainer.x1 = elem)} type="number" min="0" max="2000" step={step} value={x} onChange={(e)=>{setX(e.target.value)}} /></div>
                       <div><b>y: </b><input ref={elem =>(nodeContainer.y1 = elem)} type="number" min="0" max="2000" step={step} value={y} onChange={(e)=>{setY(e.target.value)}} /></div>
                   </div>

                   <div>
                       <div><b>x: </b><input ref={elem =>(nodeContainer.x2 = elem)} type="number" min="0" max="2000" step={step} value={x} onChange={(e)=>{setX(e.target.value)}} /></div>
                       <div><b>y: </b><input ref={elem =>(nodeContainer.y2 = elem)} type="number" min="0" max="2000" step={step} value={y} onChange={(e)=>{setY(e.target.value)}} /></div>
                   </div>

                   <div>
                       <div><b>x: </b><input ref={elem =>(nodeContainer.x3 = elem)} type="number" min="0" max="2000" step={step} value={x} onChange={(e)=>{setX(e.target.value)}} /></div>
                       <div><b>y: </b><input ref={elem =>{nodeContainer.y3 = elem; monitor = obj}} type="number" min="0" max="2000" step={step} value={y} onChange={(e)=>{setY(e.target.value)}} /></div>
                   </div>
               </div>
               <hr></hr>
               <div className="pad">
                   <div><FaAngleUp></FaAngleUp></div>
                   <div>
                       <div><FaAngleLeft></FaAngleLeft></div>
                       <div><FaAngleRight></FaAngleRight></div>
                   </div>
                   <div><FaAngleDown></FaAngleDown></div>
               </div>
           </div>
            
        )}
        </ThemeContext.Consumer>
        
    )
}
